@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
#loaderContainer {
    text-align: center;
    padding: 38vh 0;
}

@media (max-width : 900px) {

    #loaderContainer {
        padding: 32vh 0;
    }
    
}
.homeContainer {
    display:flex;
    align-content: center;
    overflow: hidden;
    -webkit-animation-name: homeAnimation;
            animation-name: homeAnimation;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
}

.headerContainer {
    position: fixed;
    z-index: 1;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 0;
    padding: 2% 5%;
    border-radius: 3em;
    pointer-events: none;
    text-align: center;
    font-style: italic;
    letter-spacing: 0.2vw;
    text-shadow: 5px 5px 5px #000000;
    -webkit-animation-name: headerAnimation;
            animation-name: headerAnimation;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
}

.headerContainer h1 {
    font-size: 4vw;
}

.headerContainer h2 {
    font-size: 2vw;
}

.imageContainer {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    border: 3px solid black;
}

.imageContainer img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    -webkit-filter: blur(6px);
            filter: blur(6px);
    webkit-filter: blur(6px);
    transition: 0.3s;
}

.imageContainer:hover img {
    -webkit-filter: blur(0px);
            filter: blur(0px);
    webkit-filter: blur(0px);
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
}

#imageHobbies img {
    width: auto;
    height: 100%;
    left: -45%;
}

.labelContainer {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0, 0.5);
    transition: 0.3s;
}

.labelContainer:hover {
    background-color: rgb(0, 0, 0, 0.0);
}

.label {
    position: absolute;
    width: 80%;
    height: auto;
    margin: auto;
    padding: 2vh 0;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.85);
    border-radius: 2vw;
    font-size: 2.5vw;
    cursor: pointer;
    transition: 0.3s;
    opacity: 0;
    color: #e6e6e6;
}

.labelContainer:hover .label {
    opacity: 1;
}

.label:hover {
    color: rgb(255, 239, 65);
}

/* ANIMATIONS */

#imageBio {
    -webkit-animation-name: imageBioMove;
            animation-name: imageBioMove;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
}

#imagePortfolio {
    -webkit-animation-name: imagePortfolioMove;
            animation-name: imagePortfolioMove;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
}

#imageHobbies {
    -webkit-animation-name: imageHobbiesMove;
            animation-name: imageHobbiesMove;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
}

@-webkit-keyframes imageBioMove {
    0% {
        left: 100vw;
        opacity: 0;
    }
    40% {
        left:0;
        opacity: 1;
    }
}

@keyframes imageBioMove {
    0% {
        left: 100vw;
        opacity: 0;
    }
    40% {
        left:0;
        opacity: 1;
    }
}

@-webkit-keyframes imagePortfolioMove {
    0% {
        left: 100vw;
        opacity: 0;
    }
    20% {
        left: 100vw;
        opacity: 0;
    }
    60% {
        left:0;
        opacity: 1;
    }
}

@keyframes imagePortfolioMove {
    0% {
        left: 100vw;
        opacity: 0;
    }
    20% {
        left: 100vw;
        opacity: 0;
    }
    60% {
        left:0;
        opacity: 1;
    }
}

@-webkit-keyframes imageHobbiesMove {
    0% {
        left: 100vw;
        opacity: 0;
    }
    40% {
        left: 100vw;
        opacity: 0;
    }
    80% {
        left:0;
        opacity: 1;
    }
}

@keyframes imageHobbiesMove {
    0% {
        left: 100vw;
        opacity: 0;
    }
    40% {
        left: 100vw;
        opacity: 0;
    }
    80% {
        left:0;
        opacity: 1;
    }
}

@-webkit-keyframes headerAnimation {
    0% {
        bottom: -3vh;
        opacity: 0;
    }
    80% {
        bottom: -3vh;
        opacity: 0;
    }
    100% {
        bottom: 0;
        opacity: 1;
    }
}

@keyframes headerAnimation {
    0% {
        bottom: -3vh;
        opacity: 0;
    }
    80% {
        bottom: -3vh;
        opacity: 0;
    }
    100% {
        bottom: 0;
        opacity: 1;
    }
}

@-webkit-keyframes homeAnimation {
    0% {
        pointer-events: none;
    }
    100% {
        pointer-events: auto;
    }
}

@keyframes homeAnimation {
    0% {
        pointer-events: none;
    }
    100% {
        pointer-events: auto;
    }
}
#biographyContainer {
    left: 0;
    width: 0;
    height: 100vh;
}

#returnBio {
    width: 0%;
    height: 10%;
}
#portfolioContainer {
    left: 0;
    width: 100%;
    height: 0;
}

#returnPortfolio {
    width: 100%;
    height: 0;
}

.contentTitle h1 {
    font-size: 2.5vw;
}

.portfolioContentSection {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 2vw;
}

.sectionInfo {
    height: 15vw;
    width: 35vw;
    padding: 1vw;
    text-align: left;
}

.sectionInfo h2 {
    font-size: 1.5vw;
    margin-bottom: 2vh;
}

.sectionInfo p {
    font-size: 1.1vw;
}

.sectionImage {
    position: relative;
    width: 15vw;
    height: 15vw;
    background-color: #282c34;
}

.sectionImage img {
    width: 100%;
    height: auto;
}

.imageHoverDiv {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: #282c343a;
    transition: 0.3s;
}

.imageHoverDiv:hover {
    opacity: 1;
}

.imageHoverDiv a {
    text-decoration: none;
    color: #e6e6e6;
}

.linkDiv {
    position: relative;
    top: 0;
    width: 100%;
    height: 50%;
    opacity: 0;
    background-color: #282c348e;
    transition: 0.3s;
}

.linkDiv:hover {
    opacity: 1;
}

.linkDiv h2 {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    text-align: center;
    font-size: 200%;
}
#hobbiesContainer {
    right: 0;
    width: 0;
    height: 100vh;
}

#returnHobbies {
    width: 0%;
    height: 10%;
}
.App {
    width: 100%;
    height: 100%;
    position: relative;
    color: #e6e6e6;
    text-align: center;
}

.pageContainer {
    position: absolute;
    z-index: 1;
    top: 0;
    background-color: #e6e6e6;
    overflow: hidden;
    transition: 0.7s;
}

.pageWrapper {
    overflow-y: auto;
    height: 100%;
}

.divBack {
    position: relative;
    width: 100%;
    height: 30vh;
    background-color: #282c34;
    text-align: right;
    padding: 14vh 14vw;
}

.divBack::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 150%;
    background: inherit;
    top: 0;
    right: 0;
    -webkit-transform-origin: top-left;
            transform-origin: top-left;
    -webkit-transform: skewY(3deg);
            transform: skewY(3deg);
}

.titleHeading {
    position: relative;
    z-index: 1;
    pointer-events: none;
    font-size: 6vw;
    text-shadow: 5px 5px 5px #000000;
}

.pageContent {
    color: black;
    margin: 0 20%;
}

.contentTitle {
    margin-top: 20vh;
    text-align: left;
}

.return {
    position: fixed;
    z-index: 2;
    background-color: #e6e6e65b;
    transition: 0.7s;
}

.return:hover {
    background-color: #e6e6e6;
}

.preview {
    margin-top: 25%;
    font-size: 1.5vw;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    height: 100%;
}

body {
    height: 100%;
    font-size: 1em;
    font-family: 'Roboto', sans-serif;
    background-color: #282c34;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: antialiased;
}

